.App {
  text-align: center;
}

body{
   background-image: radial-gradient(circle, #c8cacd, #b1bcc5, #96b0bc, #7aa4ae, #5d989d);
}
button{
  outline: none;
}
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: lightblue; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(130, 175, 190); 
}